<template>
    <div class="cresuccess main-content">
        <div class="text-success">
            Wallet imported Successful
        </div>
        <div class="success-img">
            <div class="img-icon">
                <i class="fa fa-chenggong" style="font-size: 70px;text-align: center;line-height: 260px;color:#4c4c57;"></i>
                <img class="img1" src="../../assets/img/success.png" alt="">
                <img class="img2" src="../../assets/img/success.png" alt="">
                <img class="img3" src="../../assets/img/success.png" alt="">
            </div>
            
        </div>
        <div class="cresuccess-button">
            
            <div v-if="$route.query.type=='1'">
                <p>钱包创建成功，请在使用前将助记词储存到安全地方做好钱包备份</p>
                <mt-button @click.native="newpurse_S(1)" :class="selected_newpurse?'cj-button selected':'cj-button'">
                    备份钱包
                </mt-button>
                <mt-button @click.native="newpurse_S(2)"  :class="selected_import?'cj-button selected':'cj-button'">
                    稍后备份
                </mt-button>
            </div>
            <div v-if="$route.query.type=='2'" style="padding-top:40px;">
                <p style="padding:0 26px">钱包导入成功，请妥善保管好你的密码</p>
                <mt-button @click.native="newpurse_S(3)" class="cj-button selected">
                    确定
                </mt-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                selected_newpurse:true,
                selected_import:false
            }
        },
        methods:{
            newpurse_S(val){
                if(val == 1){
                    this.selected_newpurse = true;
                    this.selected_import=false;
                    this.$router.push('/backups')
                }
                if(val == 2){
                    this.selected_newpurse = false;
                    this.selected_import=true;
                    this.$router.push('/')
                }
                if(val == 3){
                    this.$router.push('/')
                }
            }
        }
    }
</script>

<style lang="less">
    .cresuccess{
        .text-success{
            padding-top: 62px;
            font-family:Lato-Regular;
            font-size:36px;
            color:#ffffff;
            letter-spacing:0;
            text-align:center;
            line-height:48px;
        }
        .success-img{
            width: 100%;
            height:260px;
            margin-top: 73px;
            margin-bottom:92px;
            .img-icon{
                // width:26rem;
                text-align: center;
                position: relative;
                img{
                    width:260px;
                    position: absolute;
                    left:50%;
                    top:0;
                    margin-left: -130px;
                }
                .img2{
                    transform: scale(0.7);
                }
                .img3{
                    transform: scale(0.4);
                }
            }
        }
        .cresuccess-button{
            text-align: center;
            width:100%;
            margin-top: 98px;
            padding:0 30px;
            margin-bottom: 50px;
            p{
                font-family:PingFangSC-Semibold;
                letter-spacing:1px;
                font-size:7.5px;
                color:#66688f;
                text-align:center;
                line-height:16px;
                margin-bottom: 17px;
                padding: 0 56px;
            }
            .cj-button{
                border:none;
                background: none;
                box-shadow: none;
                width: 100%;
            }
            .cj-button:hover{
                background-image:linear-gradient(120deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:100px;
                height:50px;
                border:none;
                margin-bottom: 10px;
                color: #fff;
            }
            .selected{
                // background-image:linear-gradient(-131deg, #fc5d9f 0%, #5863fc 100%);
                background-image:linear-gradient(120deg, #fc5d9f 0%, #5863fc 100%);
                border-radius:10px;
                height:50px;
                border:none;
                margin-bottom: 10px;
                color: #fff;
            }
        }
    }
</style>



