                    <span class="name-sp">{{ item.name }}</span><span class="number-sp float-right">{{ item.count }}</span>
                </div>
                <div class="zichan1 clear">
                    <span class="name-sp">≈{{ item.price }} CNY</span><span class="number-sp float-right">{{ item.totalPrice }} CNY</span>
                </div>
